.wp-block-blocks-course-price-table {
	display: block;

	
	@for $i from 1 through 6 {
		&.has-#{$i}-columns {
			/* stylelint-disable-next-line selector-class-pattern */
			> .block-editor-inner-blocks > .block-editor-block-list__layout > {

				[data-type="blocks-course/price-block"] {
					width: calc(100% / #{$i} - 20px);
      				// background: linear-gradient(to bottom, skyblue 38%, white 38%, white 100%);
				}

				.block-list-appender {
					align-self: stretch;
					width: calc(100% / #{$i} - 20px);
					border: 1px dashed;
					display: flex;
					align-items: center;
					justify-content: center;
					min-height: 70px;
				}
			}
		}
	}
	/* stylelint-disable-next-line selector-class-pattern */
	> .block-editor-inner-blocks > .block-editor-block-list__layout {
		display: flex;
		flex-wrap: wrap;
		margin-left: -10px;
		margin-right: -10px;

		[data-type="blocks-course/price-block"] {
			max-width: none;
			margin: 10;
			text-align: center;
			border-radius: 5px;
		}

		> [data-type="blocks-course/price-block"] {
			margin: 10px;
			background-color: rgb(255, 255, 255);
			box-shadow: 5px 5px 10px rgb(160, 217, 208);
			// box-shadow: 5px 5px 10px rgb(160, 217, 208);
			border: none;
			background: linear-gradient(to bottom, rgb(121, 121, 219) 0%, rgb(249, 254, 255) 45%, white 100%);
			

			/* Add the styles for h4, h2, li, and button here */

			h4 {
				color: white;
				text-align: center;
				margin-bottom: 10px; /* Adjust margin for a more normal appearance */
				margin-top: 20px; /* Adjust margin for a more normal appearance */
				font-size: 24px; /* Adjust font size for a more normal appearance */
				font-weight: 700; /* Avoid using !important whenever possible */
				font-family: 'ProximaNova-Bold', Helvetica, Arial, sans-serif;
				line-height: 1.5; /* Adjust line height for a more normal appearance */
			  }
			  
			  h3 {
				color: rgb(0, 0, 0);
				margin-bottom: 10px; /* Adjust margin for a more normal appearance */
				margin-top: 0; /* Adjust margin for a more normal appearance */
				font-weight: 600;
				font-size: 48px; /* Adjust font size for a more normal appearance */
			  }
			  
			  li {
				margin-top: 20px; /* Adjust margin for a more normal appearance */
				padding-bottom: 10px;
				list-style-type: none;
				letter-spacing: 0.5px; /* Adjust letter spacing for a more normal appearance */
				line-height: 1.6; /* Adjust line height for a more normal appearance */
				font-weight: 400;
				font-size: 18px; /* Adjust font size for a more normal appearance */
			  }
			  
  
  button{
	box-sizing: border-box;
	  margin: 0;
	  padding: 15px 30px;
	  font-size: 16px;
	  border: 1px solid #27264b;
	  border-radius: 5px;
	  background-color: #fff;
	  color: #27264b;
	  cursor: pointer;
	  text-transform: uppercase;
	  transition: background-color 250ms ease-in-out;
	}
  
	button:hover {
	  background-color: rgb(0, 9, 64);
	}
  }
  
  button:hover {
	background-color: #9390db;
	color: rgb(255, 255, 255);
  }
  
		}
	}
